import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Redirect } from "react-router-dom";

import { JobFirstCreateForm } from '../components/JobForms';

// actions
import JobsActions from '../redux/redux-sauce/jobs';


class JobFirstCreateShop extends Component {
  constructor(props) {
    super(props);  
    this.state = {
    }

  this.submitCreateShop = this.submitCreateShop.bind(this);
  }

  componentDidMount() {
    this.props.getCategories()
  }

  submitCreateShop(e){
    e.preventDefault()
    this.props.createFirstJob(this.props.state.form.jobFirstCreate.values)
    this.props.history.push("/zakazchik/account/")
  }

  render() {
    let { user } = this.props
    const { categories } = this.props.state.jobs
    return (
      user && user.zakazchik ?
      <Redirect
            to={{pathname: "/zakazchik/account/", }}
          />
      :
      <div className='container-fluid shop-create'>
        <h4>
          Создание работы. Слизано с ремонтника.
        </h4>
        <JobFirstCreateForm shopName={''} shopPhone={''} shopInfo={''} handleSubmit={this.submitCreateShop}
          user={user} categories={categories}/>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  state: state,
  routing: state.routing,
  user: state.auth.user
})

const mapDispatchToProps = (dispatch) => ({
  createFirstJob: id => dispatch(JobsActions.createFirstJobRequest(id)),
  getCategories: id => dispatch(JobsActions.getCategoriesRequest(id)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(JobFirstCreateShop);
