import React, { Component } from 'react';
import { Field, reduxForm } from 'redux-form'

// actions

const renderField = ({
  input,
  label,
  placeholder,
  type,
  meta: { touched, error, warning }
}) => (
  <div>
    <label>{label}</label>
    <div>
      <input {...input} placeholder={placeholder} type={type} className="form-control"/>
      {touched &&
        ((error && <span>{error}</span>) ||
          (warning && <span>{warning}</span>))}
    </div>
  </div>
)

function JobForm2 (props) {
  let { handleSubmit, pristine, reset, submitting, categories, setData, job, jobDate } = props
  return (
    <div>
      <div>
          <Field
            component={renderField}
            name="title"
            type="text"
            label='Что нужно сделать?'
          />
        </div>
        <div>
          <label>Выберите категорию работ</label>
          <div>
            <Field name="category" component="select">
              <option />
              {categories && categories.length > 0 && categories.map(category => 
                job && job.category === category.ru_name ?
                <option defaultValue={category.id} key={category.id} >{category.ru_name}</option>
                :
                <option value={category.id} key={category.id}>{category.ru_name}</option>
                )}
            </Field>
          </div>
        </div>
        <div>
          <label>Укажите объем и виды работ</label>
          <div>
            <Field name="description" component="textarea" className="form-control job-form-desc"
              placeholder='Какую работу нужно сделать? 
              - Напишите список работ.
              - Укажите объем работы в квадратных метрах, штуках или в другой подходящей единице измерения.
              - Опишите ваши пожелания и требования, если они есть.
              '
              />
          </div>
        </div>
        <div>
          <label>Когда нужно выполнить работу?</label>
          <select onChange={setData} name='jobDate'>
            <option defaultValue={'any'}>В любое время</option>
            <option value={'period'}>В период</option>
            <option value={'not later'}>Не позднее, чем</option>
          </select>
          {jobDate == 'period' && 
            <div>
              <label>C</label>
              <Field
                name="start_date"
                component='input'
                type="date"
                placeholder=""
                label="C"
              />
              <label>До</label>
              <Field
                name="end_date"
                component='input'
                type="date"
                placeholder=""
                label="До"
              />
            </div>
            }
          {jobDate == 'not later' && 
            <Field
              name="end_date"
              component='input'
              type="date"
              placeholder=""
            />
          }
        </div>
        <div>
          <Field
            name="budget"
            component={renderField}
            type="number"
            placeholder=""
            label="Бюджет до"
          />
        </div>
        <div>
          <Field
            component={renderField}
            name="address"
            type="text"
            placeholder={'Улан-Удэ'}
            value={'Улан-Удэ'}
            label='Адрес'
          />
        </div>
    </div>
  )
}

export class JobFirstCreateForm extends Component {
  constructor(props) {
   super(props);
   this.state = {
     jobDate: 'any',
   };

   this.setData = this.setData.bind(this);   
 }

  setData (e) {
    this.setState({
      [e.target.name]: e.target.value
      })
  }
  
  render () {
    let { handleSubmit, pristine, reset, submitting, user, categories } = this.props
    return (
      <form onSubmit={handleSubmit}>
        <div>
          <Field
            component={renderField}
            name="name"
            type="text"
            placeholder={''}
            value={user ? user.nickname : ''}
            defaultValue={user ? user.nickname : ''}
            label='Как к вам обращаться?'
          />
        </div>
        <div>
          <Field
            name="phone"
            component={renderField}
            type="number"
            placeholder="79148965232"
            label="Телефон"
          />
        </div>
        <JobForm2 
          handleSubmit={handleSubmit}
          pristine={pristine}
          reset={reset}
          submitting={submitting}
          categories={categories}
          setData={this.setData}
          job={null}
          jobDate={this.state.jobDate} />
        
        <div>
          <button className='btn btn-primary'
            // type="submit"
            onClick={handleSubmit}

            disabled={pristine || submitting}>
            Создать
          </button>
        </div>
      </form>
    )
  }
}

JobFirstCreateForm = reduxForm({
  form: 'jobFirstCreate' // a unique identifier for this form
})(JobFirstCreateForm)


export class JobCreateForm extends Component {
  constructor(props) {
   super(props);
   this.state = {
     jobDate: 'any',
   };

   this.setData = this.setData.bind(this);   
 }

 componentDidMount() {
  const { start_date, end_date } = this.props.initialValues
  if (start_date) {
    this.setState({...this.state, jobDate : 'period'})
  }
  if (end_date && !start_date){
    this.setState({...this.state, jobDate : 'not later'})
  }
}

  setData (e) {
    this.setState({
      [e.target.name]: e.target.value
      })
  }
  
  render () {
    let { handleSubmit, pristine, reset, submitting, categories, job } = this.props
    return (
      <div>
        <JobForm2 
          handleSubmit={handleSubmit}
          pristine={pristine}
          reset={reset}
          submitting={submitting}
          categories={categories}
          setData={this.setData}
          job={job}
          jobDate={this.state.jobDate} />
        <div>
          <button className='btn btn-primary'
            onClick={handleSubmit}

            disabled={pristine || submitting}>
            Создать
          </button>
        </div>
      </div>
    )
  }
}

JobCreateForm = reduxForm({
  form: 'jobCreate', // a unique identifier for this form
})(JobCreateForm)


