import React, { Component } from 'react';
import { connect } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom'
import { url } from '../redux/api/endpoints';
import { getObjectbyId, toggleArray } from '../components/utils'

// actions
import JobsActions from '../redux/redux-sauce/jobs';
import IsubsActions from '../redux/redux-sauce/isubs';


function JobBlock (props) {
  const { job } = props
  return (
    <div className='job-block'>
      <div className='row'>
        <div className='col-8'>
          <a href='' className='job-title'>{job.title}</a>
        </div>
        <div className='col-4'>
          <p className='job-budget'>{job.budget}р</p>
        </div>
      </div>
      <p className='job-description'>{job.category}</p>
      <p className='job-description'>{job.description}</p>
      <p className='job-address'>{job.address}</p>
      {job.start_date ? 
        <p className='job-address'>c {job.start_date} до {job.end_date}</p>
        :
        job.end_date ?
          <p className='job-address'>До {job.end_date}</p>
          :
          <p className='job-address'>В любое время</p>
      }
      <div className='row'>
        <div className='col-6'>          
          <p className='job-zakazchik'>{job.zakazchik}</p>
        </div>
        <div className='col-6'>
          <p className='job-created-ago'>{job.created_ago}</p>
        </div>
      </div>
    </div>
  )
}


function Categories (props){
  const { categories } = props
  
  return (
    <div className='block-categories'>
      {categories.length > 0 && categories.map(category => 
        <div className={props.filterCategories.includes(category.id) ? 'btn-category btn-category-active' : 'btn-category' }
          key={category.id} onClick={props.clickCategory} data-id={category.id}>
          {category.ru_name}
        </div>
        )}
      <div className='clearfix'></div>
    </div>
  )
}

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      needToRefresh: false,
    }
    this.clickCategory = this.clickCategory.bind(this);
    this.clickSubscribe = this.clickSubscribe.bind(this);
    this.clickUnsubscribe = this.clickUnsubscribe.bind(this);
    this.clickSubscribeAll = this.clickSubscribeAll.bind(this);
  }

  componentDidMount() {
    this.props.getJobs()
  }

  clickCategory(e) {
    const { id } = e.target.dataset
    this.props.toggleFilterCategories(id)
    this.setState({...this.state, needToRefresh: true})
  }

  clickSubscribe(){
    this.props.createOrChangeIsub({categories: this.props.filterCategories})
  }

  clickSubscribeAll(){
    let categories = []
    this.props.state.jobs.categories.map(cat => categories = toggleArray(categories, cat.id))
    this.props.createOrChangeIsub({categories: categories})
  }

  clickUnsubscribe(){
    this.props.createOrChangeIsub({})
  }

  refreshList () {
    if (!this.props.state.jobs.listFetching && this.state.needToRefresh){
      setTimeout(() => {
        this.setState({...this.state, needToRefresh: false})
        const { filterCategories } = this.props
        this.props.getJobs({category: filterCategories})
      }, 10)
    }
  }

  render() {
    this.refreshList()
    const { jobs, categories, listFetching, errorList } = this.props.state.jobs
    const { filterCategories, isub } = this.props.state.isubs
    // console.log(this.props.state)
    return (
        <div className='container-fluid '>
          <div className=''>
            <div className='block-subscription'>
              {filterCategories.length > 0 
                ? <button className='btn' onClick={this.clickSubscribe}>
                    Подписаться ({filterCategories.length})
                  </button>
                : isub && isub.categories < 1 && 
                  <button className='btn' onClick={this.clickSubscribeAll}>
                    Подписаться на все категории
                  </button>
              }

              {categories && isub && isub.categories.length > 0 &&
                <div>
                  <p className='subscription-cats'>
                    Вы подписаны на {isub.categories.map(cat =>
                      cat.ru_name + ' '
                    )}
                  </p>
                  <button className='btn' onClick={this.clickUnsubscribe}>Отписаться</button>
                </div>
              }

            </div>
            <Categories 
              categories={categories} 
              getJobs={this.props.getJobs} 
              filterCategories={filterCategories}
              toggleFilterCategories={this.props.toggleFilterCategories}
              clickCategory={this.clickCategory}
            />

            <div className='jobs-block'>
              {listFetching && <p>Loading...</p>}
              {jobs.length > 0 && jobs.map(job =>
                <JobBlock job={job} key={job.id}/>
                )}
            </div>
          </div>
        </div>
      // </div>
    );
  }
}

const mapStateToProps = (state) => ({
  notifications: state.notifications,
  state: state,
  filterCategories: state.isubs.filterCategories
})

const mapDispatchToProps = (dispatch) => ({
  getCategories: query => dispatch(JobsActions.getCategoriesRequest(query)),
  getJobs: query => dispatch(JobsActions.getJobsRequest(query)),

  toggleFilterCategories: catId => dispatch(IsubsActions.toggleFilterCategories(catId)),
  createOrChangeIsub: catId => dispatch(IsubsActions.createOrChangeIsubRequest(catId)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Main);
